body {
  font-family: "helvetica", "sans-serif";
  margin: 0;
  width: 99%;
}

/* start: global style rules */
h1,
h2,
h3,
h4,
h5 {
  line-height: 1.5;
  font-weight: 300;
  margin: 0.5em 0;
}

h1 {
  font-size: 2.5em;
}

h2 {
  font-size: 2em;
}

h3 {
  font-size: 1.6em;
}

h4 {
  font-size: 1.4em;
}

h5 {
  font-size: 1.2em;
}

a {
  text-decoration: none;
}

p,
label,
strong {
  line-height: 2;
  font-size: 0.85em;
  font-weight: 300;
}

.flex {
  display: flex;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  justify-content: space-between;
}

.badge {
  padding: 9px 20px;
  color: #eee;
  display: inline-block;
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  font-size: 0.75em;
}

.badge.new {
  background-color: #ff205f;
}

.badge.strategy {
  background-color: #4eae60;
}

.badge.racing {
  background-color: #694eae;
}

.badge.rpg,
.badge.adventure {
  background-color: #40abf5;
}

.badge.tournaments {
  background-color: #fb6e10;
}

.badge.premium-tournament {
  background-color: #ffb320;
  color: #252525;
}

.shade {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
}
/* end: global style rules */

/* start: header styles */
header {
  background-color: #131313;
  padding: 20px 0;
  position: relative;
  border-bottom: 1px solid #ffb320;
}

.hamburger-menu {
  background-color: transparent;
  border: 1px solid #ffb320;
  padding: 3px 5px;
  width: 30px;
  cursor: pointer;
  display: none;
}

.hamburger-menu .strip {
  display: block;
  height: 1px;
  background-color: #ffb320;
  margin: 4px 0;
}

nav ul {
  list-style-type: none;
  padding: 0;
  margin: 10px 0 0;
}

nav ul li {
  display: inline-block;
  font-size: 1em;
  margin: 0 18px;
}

nav ul li a {
  color: #eee;
  font-weight: 600;
}

nav ul li a:hover {
  color: #ffb320;
}

#login-register-button {
  background-color: #ffb320;
  color: #131313;
  border-radius: 10px;
  padding: 5px 5px;
  font-size: 0.85em;
  font-weight: 600;
}
/* end: header styles */

/* start: hero styles */
#hero-image {
  /* background: url("https://onclickwebdesign.com/wp-content/uploads/purple_monkey_bg.jpg")
    top center no-repeat; */
    background:url('../img/game_image.png') ;
  height: 686px;
  margin-bottom: 34px;
}

.hero-marketing-text {
  max-width: 1200px;
  margin: -30px auto 0;
  position: absolute;
  top: 40%;
  left: 0;
  right: 0;
  padding: 0 20px;
}

.hero-marketing-text h1 {
  color: #eee;
  font-size: 3.5em;
  font-weight: 300;
  margin: 0.5em 0;
}

.hero-marketing-text span {
  color: #ffb320;
}

.hero-marketing-text h5 {
  color: #eee;
  font-size: 0.85em;
  font-weight: 300;
  max-width: 500px;
  line-height: 2;
  margin-bottom: 2.5em;
}

.hero-marketing-text button {
  background-color: #ffb320;
  color: #131313;
  border-radius: 25px;
  padding: 15px 25px;
  font-size: 0.85em;
  font-weight: 600;
  border: none;
  cursor: pointer;
}
/* end: hero styles */

/* start: latest news styles */
#latest-news {
  margin-bottom: 75px;
}

#latest-news .flex {
  justify-content: flex-start;
  max-width: 100%;
  padding: 0;
}

#latest-news .flex h5 {
  flex-basis: 25%;
  background-color: #ffb320;
  padding: 25px;
  margin: 0;
  text-align: right;
  color: #eee;
  font-size: 1.2em;
}

#latest-news-container {
  background-color: #131313;
  position: relative;
  flex-basis: 75%;
  padding: 25px;
}

#latest-news-container .badge {
  margin-right: 20px;
}

#latest-news-container .latest-news-text {
  color: #eee;
}
/* end: latest news styles */

/* start: game types styles */
#game-types-boxes {
  margin-bottom: 75px;
}

#game-types-boxes .box {
  flex-basis: 25%;
  position: relative;
  height: 380px;
}

#game-types-boxes .box.new {
  background: url("https://onclickwebdesign.com/wp-content/uploads/wow_lady.jpg")
    top center no-repeat;
  background-size: cover;
  cursor: pointer;
}

#game-types-boxes .box.strategy {
  background: url("https://onclickwebdesign.com/wp-content/uploads/robotic_ninja.jpg")
    top center no-repeat;
  background-size: cover;
  cursor: pointer;
}

#game-types-boxes .box.rpg {
  background: url("https://onclickwebdesign.com/wp-content/uploads/jeep_armed.jpg")
    top center no-repeat;
  background-size: cover;
  cursor: pointer;
}

#game-types-boxes .box.racing {
  background: url("https://onclickwebdesign.com/wp-content/uploads/sports_car.jpg")
    top center no-repeat;
  background-size: cover;
  cursor: pointer;
}

#game-types-boxes .box .badge {
  position: relative;
  z-index: 2;
  top: 20px;
  left: 20px;
}

#game-types-boxes .box .contents {
  position: absolute;
  z-index: 2;
  bottom: 20px;
  left: 0;
  color: #eee;
  padding: 0 35px 0 25px;
}

#game-types-boxes .box .contents a {
  font-size: 0.8em;
  font-weight: 300;
  color: #999;
}
/* end: game types styles */

/* start: recent games styles */
#recent-games {
  background: url("https://onclickwebdesign.com/wp-content/uploads/recent_games_bg.png")
    top center no-repeat;
  background-color: #eef2f6;
  border-top: 1px solid #d6dee7;
  border-bottom: 1px solid #d6dee7;
  padding: 70px 0 120px;
}

#recent-games h1 {
  text-align: center;
}

#recent-games .box {
  position: relative;
  flex-basis: 31%;
  background-color: #fff;
}

#recent-games .box .badge {
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 2;
}

#recent-games .box img {
  width: 100%;
}

#recent-games .box .box-lower-section {
  padding: 20px;
}

#recent-games .box .box-lower-section p {
  color: #999;
}

#recent-games .box .box-lower-section a {
  color: #999;
  font-size: 0.8em;
}
/* end: recent games styles */

/* start: tournaments styles */
#tournaments {
  background: url("https://onclickwebdesign.com/wp-content/uploads/footer_bg_pattern.png")
    0 0 repeat;
  padding: 125px 0 105px;
}

#tournaments .flex {
  position: relative;
}

#tournaments .badge {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

#tournaments .badge.tournaments {
  top: -32px;
  left: 15px;
}

#tournaments .box {
  background-color: #252525;
  padding: 70px 20px 40px;
  flex-basis: 45%;
  position: relative;
}

#tournaments .box .tournaments-image {
  float: left;
  width: 30%;
}

#tournaments .box .tournaments-image img {
  width: 100%;
}

#tournaments .box .tournaments-content {
  float: left;
  margin-left: 25px;
  width: 60%;
}

#tournaments .box .tournaments-content h3 {
  color: #ffb320;
  margin-top: 0;
}

#tournaments .box .tournaments-content label {
  color: #999;
  font-size: 0.75em;
}

#tournaments .box .tournaments-content label.prizes {
  color: #ffb320;
  display: inline-block;
  margin-top: 12px;
}

#tournaments .box .tournaments-content strong {
  color: #eee;
  font-size: 0.75em;
}
/* end: tournaments styles */

/* start: recent reviews styles */
#recent-reviews {
  background: url("https://onclickwebdesign.com/wp-content/uploads/recent_reviews_bg.png")
    top right no-repeat;
  background-size: cover;
  padding: 75px 0 50px;
}

#recent-reviews h1 {
  text-align: center;
  margin-bottom: 1.65em;
}

#recent-reviews .box {
  flex-basis: 23%;
  position: relative;
  text-align: center;
}

#recent-reviews .box .rating-badge {
  border-radius: 28px;
  padding: 13px;
  font-size: 1.1em;
  color: #eee;
  font-weight: 600;
  background-color: #ffb320;
  position: absolute;
  top: -30px;
  left: 30px;
}

#recent-reviews .box .rating-badge.purple {
  background-color: #694eae;
}

#recent-reviews .box .rating-badge.green {
  background-color: #4eae60;
}

#recent-reviews .box .rating-badge.violet {
  background-color: #ff20ae;
}

#recent-reviews .box .recent-reviews-image {
  margin-bottom: 0.75em;
  overflow: hidden;
}

#recent-reviews .box .recent-reviews-image img {
  width: 100%;
}

#recent-reviews .box p {
  color: #999;
}
/* end: recent reviews styles */

/* start: posts/comments styles */
#posts-comments {
  background: url("https://onclickwebdesign.com/wp-content/uploads/footer_bg_pattern.png")
    0 0 repeat;
  padding: 75px 0;
}

#posts-comments .game-warrior {
  flex-basis: 32%;
  position: relative;
}

#posts-comments .game-warrior p {
  color: #eee;
  font-weight: 600;
}

#posts-comments .game-warrior img {
  margin-bottom: 0.5em;
}

#posts-comments .game-warrior img.footer-graphic {
  position: absolute;
  margin-bottom: 0;
  width: 115%;
}

#posts-comments .posts-comments-box {
  flex-basis: 28%;
  background-color: #252525;
  padding: 20px 20px 25px;
  border: 1px solid #4a4a4a;
}

#posts-comments .posts-comments-box h3 {
  color: #eee;
}

.post-item {
  margin-top: 25px;
}

.post-item:first-child {
  margin-top: 0;
}

.post-item:after {
  content: "";
  display: table;
  clear: both;
}

.post-item img {
  float: left;
  width: 30%;
}

.post-item > div {
  float: left;
  margin-left: 5%;
  width: 65%;
}

.post-item > div h5 {
  color: #ffb320;
  font-size: 0.8em;
  margin-top: 0;
}

.post-item > div p {
  color: #eee;
  margin: 5px 0;
  font-size: 0.8em;
}

.post-item > div small {
  color: #999;
  font-size: 0.75em;
}

.comments-item {
  margin-top: 1em;
}

.comments-item:first-child {
  margin-top: 0;
}

.comments-item:after {
  content: "";
  display: table;
  clear: both;
}

.comments-item img {
  width: 23%;
  height: auto;
  border-radius: 50%;
  float: left;
}

.comments-item > div {
  float: left;
  margin-left: 7%;
  width: 70%;
}

.comments-item > div p {
  color: #eee;
  font-size: 0.8em;
  margin: 0 0 5px;
}

.comments-item > div p span {
  color: #999;
}

.comments-item > div p span.author {
  color: #e92159;
}

.comments-item > div h5 {
  color: #ffb320;
  font-size: 0.8em;
  margin-top: 0;
}
/* end: posts/comments styles */

/* start: footer styles */
footer {
  background-color: #252525;
  padding: 25px 0 20px;
}

footer small {
  color: #ddd;
  font-size: 0.9em;
}

footer small .footer-heart {
  font-size: 16px;
}

footer small a {
  color: #007bdc;
}

footer ul {
  list-style-type: none;
  margin: 0;
}

footer ul li {
  display: inline-block;
  margin: 0 12px;
}

footer ul li a {
  color: #ddd;
  font-size: 0.8em;
}
/* end: footer styles */

/* start: media queries */
@media (max-width: 1200px) {
  .flex {
    max-width: 1000px;
  }

  .latest-news-item {
    font-size: 0.8em;
  }

  #tournaments .box .tournaments-content {
    margin-left: 15px;
  }

  #tournaments .box .tournaments-content h3 {
    font-size: 1.4em;
    margin-bottom: 0.2em;
  }
}

@media (max-width: 991px) {
  .flex {
    max-width: 720px;
  }

  nav ul li {
    margin: 0 12px;
    font-size: 0.85em;
  }

  #latest-news .flex h5 {
    width: 15%;
  }

  #game-types-boxes .flex {
    flex-wrap: wrap;
  }

  #game-types-boxes .box {
    flex-basis: 50%;
  }

  #tournaments .box {
    flex-basis: 42%;
  }

  #tournaments .box .tournaments-image {
    float: none;
    width: 100%;
    margin-bottom: 10px;
    height: 250px;
    overflow: hidden;
  }

  #tournaments .box .tournaments-content {
    margin-left: 0;
    width: 100%;
  }

  #recent-reviews {
    background: none;
  }

  #recent-reviews .flex {
    flex-wrap: wrap;
  }

  #recent-reviews .box {
    flex-basis: 48%;
    margin-bottom: 45px;
  }

  #posts-comments .flex {
    flex-wrap: wrap;
  }

  #posts-comments .game-warrior {
    flex-basis: 100%;
    margin-bottom: 40px;
  }

  #posts-comments .posts-comments-box {
    flex-basis: 43%;
  }

  #posts-comments .game-warrior img.footer-graphic {
    display: none;
  }

  footer .flex {
    flex-wrap: wrap-reverse;
  }

  footer small {
    flex-basis: 100%;
    font-size: 0.75em;
    text-align: center;
    margin-top: 20px;
    line-height: 1.5;
  }

  footer ul {
    flex-basis: 100%;
    text-align: center;
    padding: 0;
  }
} /* end 991px */

@media (max-width: 767px) {
  h4 {
    font-size: 1.2em;
  }

  .flex {
    max-width: 540px;
  }

  nav ul {
    display: none;
    position: absolute;
    top: 101%;
    width: 100%;
    background-color: #131313;
    left: 0;
    padding: 0;
    margin: 0;
    z-index: 1;
  }

  nav ul li {
    display: block;
    padding: 15px 0 15px 20px;
    border-bottom: 1px solid #ffb320;
    margin: 0;
  }

  nav ul li a {
    font-size: 1em;
  }

  .hamburger-menu {
    display: block;
    margin-top: 2px;
  }

  #hero-image {
    height: 650px;
    background-size: cover;
  }

  .hero-marketing-text {
    margin-top: -85px;
  }

  .hero-marketing-text h1 {
    font-size: 2.5em;
  }

  #latest-news .flex h5 {
    display: none;
  }

  #latest-news-container {
    flex-basis: 100%;
  }

  #recent-games {
    padding: 60px 0;
  }

  #game-types-boxes .box,
  #recent-games .box {
    flex-basis: 100%;
    margin-bottom: 30px;
  }

  #recent-games .flex,
  #tournaments .flex {
    flex-wrap: wrap;
  }

  #tournaments .box {
    flex-basis: 100%;
    margin-bottom: 20px;
  }

  #tournaments .box .tournaments-image {
    height: 400px;
  }

  #recent-reviews .box {
    flex-basis: 100%;
  }

  #recent-reviews .box .recent-reviews-image {
    height: 400px;
  }

  #posts-comments .posts-comments-box {
    flex-basis: 100%;
    margin-bottom: 40px;
  }

  footer ul li {
    margin: 0 10px;
  }

  footer ul li a {
    font-size: 0.75em;
  }
} /* end 767px */

@media (max-width: 400px) {
  .logo img {
    width: 150px;
    margin-top: 2px;
  }

  #login-register-button {
    padding: 10px;
    font-size: 0.65em;
  }

  #tournaments .box .tournaments-image {
    height: 220px;
  }
}

.worldcolor {
  color: #ffb320;
}
#recent-reviews h1 {
  color: white;
}
.logotext {
  color: #eee;
  font-size: 24px;
  text-decoration: none;
  font-weight: 900;
  text-decoration: none !important;
}
.logotext:hover {
  color: #ffb320 !important;
}
.logo img {
  width: 38px;
  margin-top: -9px;
}

@media (max-width: 400px) {
  .logo img {
    width: 24px;
    margin-top: -8px;
  }
}
@media (max-width: 400px) {
  .logotext {
    color: #eee;
    font-size: 15px;
    text-decoration: none;
    /* margin-top: 20px !important; */
  }
}

@media (max-width: 400px) {
  #login-register-button {
    height: 34px !important;
    width: 121px !important;
  }
}
@media (max-width: 767px) {
  #login-register-button {
    height: 34px !important;
    width: 134px !important;
  }
}
@media (max-width: 767px) {
  .logotext {
    color: #eee;
    font-size: 15px;
    text-decoration: none;
  }
}

h4 {
  font-size: 1.4em;
  color: white;
}
#game-types-boxes .box .badge {
  width: 146px;
}
.game-area{
  background-color: #eee !important;
}